import React, {useEffect,useState} from 'react';
import axios from "axios";
import {Link, useLocation, useParams,useNavigate} from 'react-router-dom';
import env from "react-dotenv";
import LeaveMngtDash from "./LeaveMngtDash";


function LeaveAllotment() {

 // let Base_URL = env.API_URL;

const usenavigate = useNavigate();
useEffect(()=>{

},[]);
const navigate = useNavigate();
const [data,setData] = useState([])
useEffect(()=>{
axios.get(`http://crmtesting.tdc3030.com/api/Chat/FetchAllotment`)
.then((res) => {
//console.log(res.data);
setData(res.data)
})
});
    
    return(
    <>
    <LeaveMngtDash/>
    <div id="depLeave" className="scrollmenu">
    {/* <div onClick={() => navigate("/table")}>
    <i class="fa fa-arrow-circle-left"></i>                        
    </div> */}
    <h1><u>Leave Allotment</u></h1>
    <div>
    <table>
    <thead>
    <tr >
    <th>Sl.No</th>
    <th>Employee</th>
    <th>Allotted</th>
    <th>Casual</th>
    <th>Paid</th>
    <th>UnPaid</th>
    <th>Sick</th>
    <th>HalfDay</th>
    <th>From</th>
    <th>To</th>
    <th>Total</th>
    <th>Remaining</th>
    </tr>
    </thead>
    <tbody>
     {
          data.map((data, index) => (
                 <tr key={index}>
                    <td>{index+1}</td>
                    <td>{data.firstName}</td>
                    <td>{data.allotted}</td>  
                    <td>{data.casual}</td>
                    <td>{data.paid}</td>
                    <td>{data.unpaid}</td>
                    <td>{data.sick}</td>
                    <td>{data.halfday}</td>
                    <td>{data.fromdate}</td> 
                    <td>{data.todate}</td> 
                    <td>{parseFloat(data.casual)+parseFloat(data.paid)+parseFloat(data.unpaid)+parseFloat(data.sick)+parseFloat(data.halfday)}</td>
                    <td><mark><strong>{data.allotted -(data.casual)-(data.paid)-(data.unpaid)-(data.sick)-(data.halfday)}</strong></mark></td>
                 </tr>))
     }
    </tbody>
    </table>
    </div>
    </div>
  </>
    );
}
export default LeaveAllotment;