import React, {useState, useEffect} from 'react';
import axios from "axios";
import {useNavigate, useLocation} from 'react-router-dom';
import env from "react-dotenv";
import EmpMngtDash from "./EmpMngtDash";

function ViewPosition(){
  const navigate = useNavigate();

  var config = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'accept': '*/*',
    },
};

//let Base_URL = env.API_URL;

const [formErrors, setFormErrors] = useState({});

const validate = () => {
const errors = {};
const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;

if (!employeeLogin.position) {
    errors.position = "Name is required";
}
if (employeeLogin.position && !nameRegex.test(employeeLogin.position)) {
errors.position = "Give proper name";
}
return errors;
};

const validateOnChange = (name, value) => {
  const errors = {};
  const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;
 
  if (name == "position" && !value) {
    errors.position = "Name is required";
  }
  if (name == "position" && value && !nameRegex.test(value)) {
    errors.position = "Give proper name";
  } 
  return errors;
};

const [employeeLogin, setEmployeeLogin] = useState({
  position: "",	
  });

const resetEmployeeDetails = () => {
  let _resetEmployee = {
    position: "",
  };
  setEmployeeLogin(_resetEmployee);
};

const handleChangeInput = (e) => {
  let key = e.target.name;
  let value = e.target.value;
  const _currentData = { ...employeeLogin, [key]: value };
  setEmployeeLogin(_currentData);
  console.log(_currentData)
  const _errors = validateOnChange(e.target.name, e.target.value);
  if (Object.keys(_errors).length != 0) {
    let finalErrors = {
      ...formErrors,
      [key]: _errors[key],
    };
    setFormErrors(finalErrors);
  } else {
    let finalErrors = {
      ...formErrors,
      [key]: _errors[key],
    };
    setFormErrors(finalErrors);
  }
};


const submitdata = () => {
  // event.preventDefault();
  const _errors = validate();
  setFormErrors(_errors);

  let _requestData = {
    ...employeeLogin,
  };
  //console.log("requested Data", _requestData);
  {
    Object.keys(_errors).length == 0
      ? axios
         .post(
          `http://crmtesting.tdc3030.com/api/Chat/postPosition` ,_requestData, config)
          .then((res) => {
                   console.log(res)
                   if (res.status === 200) {
                   alert("Successfully registred");
                   window.location.reload();
                   } setFormErrors({});
                   })
          .catch((err) =>{ alert("Something went wrong")
          setFormErrors({});
        })
      : alert("Please Fill All Details");
  }
};


const [data,setData] = useState([])
const initializeEvent = () => {
axios.get(`http://crmtesting.tdc3030.com/api/Chat/getPosition`,config)
.then((res) => {
console.log(res);
setData(res.data)
})
};
useEffect(() => initializeEvent(), []);

const [position , setPosition] = useState('');
const editTest=(id)=>{
  console.log('getdata',id)
  setEmployeeLogin(id);
  setPosition(id.position)
}

console.log('eeditsdata',employeeLogin)


const updateData =()=>{
  let _requestData = {
    ...employeeLogin,
};

{
  axios.post(`http://crmtesting.tdc3030.com/api/Chat/updatePosition/`, _requestData, config)
  .then((res) => {
  console.log(res)
  if (res.status === 200) {
    alert("Successfully updated");
    window.location.reload();
    }  else Promise.reject();
     })
     .catch((err) =>{ alert("Something went wrong")
   })
  }
};

const deleteData = (id) => {
  axios.delete(`http://crmtesting.tdc3030.com/api/Chat/deletePosition/` +id)
  .then((res) => {
  if (res.status === 200) {
    alert("Successfully deleted");
    window.location.reload();
  } else Promise.reject();
  })
    .catch((err) => alert("Something went wrong"));
  };

return (
<>
<EmpMngtDash/>
<div id='depEmpDoc'>
<button className="template-preview-input add-button rhs-select col-2 phone-col-3, date, box"  value="AddPosition" id='font' data-toggle="modal" data-target="#addPosition">
<span>Add Position</span>
</button> 

<div id="tabDis" className="scrollmenu">
<table>
  <ul>
  <thead>
      <tr>
          <th>Sl.No</th>
          <th>Position</th>
          <th>Delete</th>
          <th>Edit</th>
       </tr>
  </thead>
  <tbody>
      {
          data.map((data, index)=> (
                  <tr key={index}>
                      <td>{index+1}</td> 
                      <td>{data.position}</td>
                      <td><button value="DeletePosition" onClick={()=>deleteData(data.id)} id="btnIcon"><i class='fa fa-trash' id="faIcon"></i></button> </td>
                      <td><button value="EditPosition" data-toggle="modal" data-target="#editPosition"  onClick={()=>editTest(data)} id="btnIcon"><i class='fa fa-edit' id="faIcon1"></i>
</button> </td>
                  </tr>))
      }
  </tbody>
  </ul>
  </table>
  </div>
</div>

{/*---------------------------------Add Position--------------------*/}
<div class="modal fade" id="addPosition" role="dialog">
    <div class="modal-dialog modal-sm">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal">&times;</button>
          <h4 class="modal-title">Postion Name</h4>
        </div>
        <div class="modal-body">
        <div>

        <div id='isAddPos'>
        <p className="col-10 phone-col-12">Position Name</p>
        <input className="input textsingle item" type="text" name="position" value={employeeLogin.position} placeholder='Position' onChange={handleChangeInput} error="!employeeLogin.position"/>
        <p style={{ marginTop: "1px", color: "red" }}>{formErrors.position}</p>
        </div>

        </div>
        </div>
        <div class="modal-footer" id='ft'>
        <button id='modcl' type="button" class="btn btn-info btn-sm" data-dismiss="modal" onClick={submitdata}>Submit</button>
        </div>
      </div>
    </div>
  </div>

  {/*---------------------------------Edit Position--------------------*/}
<div class="modal fade" id="editPosition" role="dialog">
    <div class="modal-dialog modal-sm">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal">&times;</button>
          <h4 class="modal-title">Postion Name</h4>
        </div>
        <div class="modal-body">
        <div>

        <div id='isAddPos'>
        <p className="col-10 phone-col-12">Position Name</p>
        <input className="input textsingle item" type="text" name="position" value={employeeLogin.position} placeholder='Position' onChange={handleChangeInput} error="!employeeLogin.position"/>
        <p style={{ marginTop: "1px", color: "red" }}>{formErrors.position}</p>
        </div>

        </div>
        </div>
        <div class="modal-footer" id='ft'>
        <button id='modcl' type="button" class="btn btn-info btn-sm" data-dismiss="modal" onClick={()=>updateData()}>Update</button>
        </div>
      </div>
    </div>
  </div>
</>
);
}
export default ViewPosition;