import React, {useState, useEffect} from 'react';
import axios from "axios";
import {useNavigate, useLocation} from 'react-router-dom';
import env from "react-dotenv";
import EmpMngtDash from "./EmpMngtDash";

function AddEmpDoc(){
  const navigate = useNavigate();

  var config = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'accept': '*/*',
    },
};

//let Base_URL = env.API_URL;

const [employeeLogin, setEmployeeLogin] = useState({
  department: "",
  name: "",
  hr_supr: "",
  emp_number: "",
  emp_post: "",
  date_hired: "",
  emp_status: "",
  attendance: "",
  job_knw_skill: "",
  qua_work: "",
  invt_movt: "",
  team_work: "",
  gen_condt: "",
  discipline: "",
  tot_score: "",
  emp_comm: "",
  comm_prf: "",	
  rev_date: "",
  rev_todate: "",
  });

const resetEmployeeDetails = () => {
  let _resetEmployee = {
  department: "",
  name: "",
  hr_supr: "",
  emp_number: "",
  emp_post: "",
  date_hired: "",
  emp_status: "",
  attendance: "",
  job_knw_skill: "",
  qua_work: "",
  invt_movt: "",
  team_work: "",
  gen_condt: "",
  discipline: "",
  tot_score: "",
  emp_comm: "",
  comm_prf: "",	
  rev_date: "",
  rev_todate: "",
  };
  setEmployeeLogin(_resetEmployee);
};

  const [data,setData] = useState([])
  const initializeEvent = () => {
  axios.get(`http://crmtesting.tdc3030.com/api/EmpReg/getEmpData`,config)
  .then((res) => {
  console.log(res);
  setData(res.data)
  })
  };
   useEffect(() => initializeEvent(), []);

  const fetchDateRng = () => {
   
    let _requestData = {
      ...employeeLogin,
    };

   console.log(_requestData)
    {
       axios
            .post(`http://crmtesting.tdc3030.com/api/EmpReg/fetchDateRng1`, _requestData,config)
            .then((res) => {
               console.log('response',res.data)
               setData(res.data)
               if (res.data != "Failed") {
                 localStorage.setItem(
                  "user_info",
                   JSON.stringify(res.data)
                 );
               }
               if (res.data == "Failed") {
                alert("Invalid Credantials")
               }
               resetEmployeeDetails();
            })
            .catch((e) => {
               if (e.res.data.message == "Incorrect Password") {
              {
                 alert("Account Not Found");
               }
              }
            })
    }
  };
  
  const handleChangeInput1 = (e) => {
    let key = e.target.name;
    let value = e.target.value;
    const _currentData = { ...employeeLogin, [key]: value };
    setEmployeeLogin(_currentData);
    console.log(_currentData)
  };

  const handleChangeInput2 = (e) => {
    let key = e.target.name;
    let value = e.target.value;
    const _currentData = { ...employeeLogin, [key]: value };
    setEmployeeLogin(_currentData);
    axios
    .post(`http://crmtesting.tdc3030.com/api/EmpReg/fetchByDpt`, _currentData,config)
    .then((res) => {
       console.log('response',res.data)
       setData(res.data)
    })
    console.log(_currentData)
  };

  const [val, setVal] = useState([])
  const handleAdd = () =>{
    const abc=[...val,[]]
    setVal(abc)
  }

  const handleChange = (onChangeValue,i) =>{
    const inputdata=[...val]
    inputdata[i]=onChangeValue.target.value;
    setVal(inputdata)
  }

  const handleDelete = (i) =>{
    const deleteVal=[...val]
    deleteVal.splice(i,1)
    setVal(deleteVal)
  }


return (
<>
<EmpMngtDash/>
<div id='depEmpDoc'>

<div class="input-group">
<label for="First Name *" >
<span>Select Employee *</span>
<select type="text" name='firstName' value={employeeLogin.firstName} id='tit1' onChange={handleChangeInput1} className="textredL1" error="!employeeLogin.firstName">
<option value="" selected disabled>Select</option>
{data.map(data => (
    <option value={data.firstName} key={data.id}>{data.firstName}</option>
  ))
}
</select>
<span style={{ marginTop: "1px", color: "red" }}></span>
</label>
</div>

<div class="input-group">
<label for="Document Type *" >
<span>Document Type</span>
<select type="text" name='department'  id='tit1' className="textredL1">
<span style={{ marginTop: "1px", color: "red" }}></span>
<option value="" selected disabled size={10}>Select</option><br />
        <option value='Accounts'>Photo</option>
        <option value='Technical'>Resume</option>
        <option value='Nontechnical'>Offer Letter</option>
        <option value='Nontechnical'>Appointment Letter</option>
        <option value='Nontechnical'>SSLC Markscard</option>
        <option value='Nontechnical'>PUC Markscard</option>
        <option value='Nontechnical'>UG Markscard</option>
        <option value='Nontechnical'>PG Markscard</option>
        <option value='Nontechnical'>Internship Certificate</option>
        <option value='Nontechnical'>Experience Letter</option>
        <option value='Nontechnical'>PAN</option>
        <option value='Nontechnical'>Bank Passbook</option>
        <option value='Nontechnical'>Other</option>
</select>
</label>
</div>

<div class="input-group">
<label for="Document Type *" >
<p className="textredfileP">Browse</p>
<input className="input textsingle item" type="file" id='tit1' name="hr_supr" className="textredfile"/>
<p style={{ marginTop: "1px", color: "red" }}></p>
</label>
</div>

<div class="input-group">
<label for="Document Type *" >
<button className="template-preview-input add-button rhs-select col-2 phone-col-3, date, box"  data-input="datetime" id='font'>
<span className="phone-hide, date">Submit</span>
</button>
</label>
</div>

<div class="input-group">
<label for="Document Type *" >
<div id="subDsg">
<button className="template-preview-input add-button rhs-select col-2 phone-col-3, date, box"  onClick={()=>handleAdd()} data-input="datetime" id='font'>
<span className="phone-hide, date">Add More</span>
</button>
</div>
{val.map((data,i)=>{
          return(
            <div>
              
<p className="textredL2" style={{marginTop: 5}}>Document Type</p>
<select type="text" name='department'  id='tit1' className="textredL2" onChange={e=>handleChange(e,i)}>
<p style={{ marginTop: "1px", color: "red" }}></p>
<option value="" selected disabled size={10}>Select</option><br />
        <option value='Accounts'>Photo</option>
        <option value='Technical'>Resume</option>
        <option value='Nontechnical'>Offer Letter</option>
        <option value='Nontechnical'>Appointment Letter</option>
        <option value='Nontechnical'>SSLC Markscard</option>
        <option value='Nontechnical'>PUC Markscard</option>
        <option value='Nontechnical'>UG Markscard</option>
        <option value='Nontechnical'>PG Markscard</option>
        <option value='Nontechnical'>Internship Certificate</option>
        <option value='Nontechnical'>Experience Letter</option>
        <option value='Nontechnical'>PAN</option>
        <option value='Nontechnical'>Bank Passbook</option>
        <option value='Nontechnical'>Other</option>
</select>
        <button className="template-preview-input add-button rhs-select col-2 phone-col-3, date, box" onClick={()=>handleDelete(i)} id='font' style={{marginLeft: 20}}>
        <span className="phone-hide, date">Delete</span>
        </button>
        </div>
          )
        })} 
</label>
</div>
</div>
</>
);
}
export default AddEmpDoc;