  if (name == "ifscCode" && !value) {
    errors.ifscCode = "This field is required";
  }
  if (name == "ifscCode" && value && !alpanumricalValue.test(value)) {
    errors.ifscCode = "Give proper value";
  } 
  if (name == "bankAccHolderName" && !value) {
    errors.bankAccHolderName = "Name is required";
  }
