      const validate = () => {
      const errors = {};
      const alpanumricalValue =  /^[a-zA-Z0-9_.-]*$/;
      const numricalValue =  /^[0-9]+$/;
      const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;
      if (!employeeDetails.incomeSRC) {
        errors.email = "This field is required";
      }
      if (employeeDetails.incomeSRC &&!alpanumricalValue.test(employeeDetails.incomeSRC)) {
        errors.incomeSRC = "Only alphanumrica values allowed";
      }
      if (!employeeDetails.paymentModeBy) {
        errors.paymentModeBy = "This field is required";
      }
      if (employeeDetails.paymentModeBy &&!nameRegex.test(employeeDetails.paymentModeBy)) {
        errors.paymentModeBy = "Invalid name convention";
      }
      if (!employeeDetails.amountPaid) {
        errors.amountPaid = "This field is required";
      }
      if (employeeDetails.amountPaid &&!numricalValue.test(employeeDetails.amountPaid)) {
        errors.amountPaid = "Only numrical values allowed";
      }
      if (!employeeDetails.paymentRecievedBy) {
        errors.paymentRecievedBy = "This field is required";
      }
      if (employeeDetails.paymentRecievedBy &&!nameRegex.test(employeeDetails.paymentRecievedBy)) {
        errors.paymentRecievedBy = "Invalid name convention";
      }
      if (!employeeDetails.gstCal) {
        errors.gstCal = "This field is required";
      }
      if (!employeeDetails.paymentMode) {
        errors.paymentMode = "This field is required";
      }
      if (!file) {
        errors.file = "image is required";
      }
  
      return errors;
    };

    const validateOnChange = (name, value) => {
        const errors = {};
        const alpanumricalValue =  /^[a-zA-Z0-9_.-]*$/;
        const numricalValue =  /^[0-9]+$/;
        const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;
        if (name == "incomeSRC" && !value) {
          errors.incomeSRC = "This field is required";
        }
        if (name == "incomeSRC" && value && !alpanumricalValue.test(value)) {
            errors.incomeSRC = "Only alphanumrica values allowed";
        }
        if (name == "paymentModeBy" && !value) {
            errors.paymentModeBy = "This field is required";
        }
        if (name == "paymentModeBy" && value && !nameRegex.test(value)) {
          errors.paymentModeBy = "Invalid name convention";
      }
        if (name == "amountPaid" && !value) {
          errors.amountPaid = "This field is required";
        }
        if (name == "amountPaid" && value && !numricalValue.test(value)) {
          errors.amountPaid = "Only numrical values allowed";
        }
        if (name == "paymentRecievedBy" && !value) {
            errors.paymentRecievedBy = "This field is required";
        }
        if (name == "paymentRecievedBy" && value && !nameRegex.test(value)) {
          errors.paymentRecievedBy = "Invalid name convention";
      }
        if (name == "gstCal" && !value) {
          errors.gstCal = "This field is required";
        }
          if (name == "paymentMode" && !value) {
            errors.paymentMode = "This field is required";
        }
        return errors;
    };

