import React, {useEffect,useState} from 'react';
import axios from "axios";
import {Link, useLocation, useParams,useNavigate} from 'react-router-dom';
import Avatar from "@material-ui/core/Avatar";
import { DynamicStar } from 'react-dynamic-star';
import { colors } from '@material-ui/core';
import Popup from 'reactjs-popup';
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import env from "react-dotenv";
import LeaveMngtDash from "./LeaveMngtDash";

function LeaveManagement() {
  const [no , setno] = useState('');
  const [result , setresult] = useState('');
  const [Editdata , setEditdata] = useState('');

  const [popup,setPop]=useState(false)
  var config = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'accept': '*/*',
    },
  };

  //let Base_URL = env.API_URL;

//   const handleClickOpen=()=>{
//     setPop(!popup)
// }
const closePopup=()=>{
    setPop(false)
}

  const handleChangeInput =(e)=>{
    setresult(e.target.value);
      }
 
const usenavigate = useNavigate();
useEffect(()=>{

},[]);

const navigate = useNavigate();
const [data,setData] = useState([])
useEffect(()=>{
axios.get(`http://crmtesting.tdc3030.com/api/Chat/FetchLeave`)
.then((res) => {
//console.log(res.data);
setData(res.data)
})
});
const editTest=(no)=>{
  setPop(!popup)
  console.log('getdata',no)
  setEditdata(no);
  setresult(no.result)
  setno(no.no)
  }
  const [style, setStyle] = useState("buttoni");
  
  const changeStyle = () => {
   // console.log("you just clicked");
    setStyle("wide");
  };

const updateData =()=>{
  let formdata = {
    'no':no,
    'result':result
  }
axios.post(`http://crmtesting.tdc3030.com/api/Chat/holiday/`, formdata, config)
.then((res) => {
console.log(res)
if (res.status === 200) {

} else Promise.reject();
})
.catch((err) => alert("Something went wrong"));
console.log(formdata)
}

const deleteTest = (no) => {
  axios.delete(`http://crmtesting.tdc3030.com/api/Chat/deleteWork/` +no)
  .then((res) => {
  if (res.status === 200) {
    alert("Done");
    window.location.reload();
  } else Promise.reject();
  })
    .catch((err) => alert("Something went wrong"));
  };

  ReactHTMLTableToExcel.format = (s, c) => {
    if (c && c['table']) {
      const html = c.table;
      const parser = new DOMParser();
      const doc = parser.parseFromString(html, 'text/html');
      const rows = doc.querySelectorAll('tr');
  
      for (const row of rows) row.removeChild(row.firstChild);
  
      c.table = doc.querySelector('table').outerHTML;
    }
  
    return s.replace(/{(\w+)}/g, (m, p) => c[p]);
  };

    return(
        <>
        <LeaveMngtDash/>
        <div id="depLeaveTbl" className="scrollmenu">
        {/*
        <div class="division35">
        <h1 id="mm"><u>Employees Leave Report</u></h1>
        </div>
    */}
        {
                    popup?
                    <div className="main">
                        <div className="main">
                            <div class="division30">
  <select value={result} name="result" onChange={(e)=>{handleChangeInput(e)}} required>
  <option value="" size={15}>Select</option>
  <option value="Accepted">Accept</option>
  <option value="Rejected">Reject</option>
  </select>
  </div>
  <div class="division30">
  <span onClick={changeStyle}><button type="submit" id="popibtn" onClick={()=>updateData()}>Submit</button></span>
  </div>
                        </div>
                    </div>:""
                }
            <div>
        <div>
   
<div id="tst" class="division90">
    <ReactHTMLTableToExcel
        id="fontRectTbl"
        className="download-table-xls-button"
        table="dataTable"
        filename="Employee Leaves"
        sheet="tablexls"
        class="fa fa-download"
        buttonText="Download"
      />
      </div>
        <div>
        <table id="dataTable">
  <thead>
      <tr>
        <th>S.No</th>
        <th>Employee</th> 
        <th>Date</th>
        <th>Login Time</th>
        {/* <th id='hh' width="18px">LogOut Time</th> */}
        {/* <th id='hh' width="20px">To</th> */}
        <th>Leave</th>
        <th>Reason</th>
          {/* <th id='hh' width="25px">Duration</th> */}
          <th>Status</th>
          <th>Remark</th>
          <th>Action</th>
      </tr>
  </thead>
  <tbody>
      {
          data.map((data, index) => (
                  <tr key={index}>
                    <td>{index+1}</td>
                    <td>{data.firstName}</td>
                    <td>{data.date}</td>
                    <td>{data.logintime}</td>
                    {/* <td width="10%" height="10px">{data.logouttime}</td> */}
                    {/* <td width="12%" height="20px">{data.todate}</td> */}
                    <td>{data.leave}</td>
                    <td>{data.reason}</td>
                      {/* <td width="8%">{data.timeDiff}</td> */}
                      <td><p id="stat" type='submit' onClick={()=>editTest(data)}><mark><strong>{data.result}</strong></mark></p></td>
                      <td>{data.remark}</td>
                      <td><i type="submit" class="fa fa-trash" id="faIcon" onClick={()=>deleteTest(data.no)} /></td>
                  </tr>))
      }
  </tbody>
  </table>
    </div>
    </div>
    </div>

<div>
    <p id="not"><strong>
      Where :
      1 = Late Login,
      0 = In time Login.
      </strong>
    </p>
   </div>
   </div>
  </>
    );
}
export default LeaveManagement;