import React, {useState, useEffect} from 'react';
import axios from "axios";
import {useNavigate, useLocation} from 'react-router-dom';
import env from "react-dotenv";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import EmpMngtDash from "./EmpMngtDash";
function ViewEmp(){

const navigate = useNavigate();

const [viewEmp, setViewEmp] = useState({
    firstName: "",
})

const resetEmployeeDetails = () => {
    let _resetEmployee = {
        firstName: "",
    };
    setViewEmp(_resetEmployee);
};

const [data,setData] = useState([])
const initializeEvent = () => { 
axios
.get(`http://crmtesting.tdc3030.com/api/Chat/getEmpData1`)
.then((res) => {
    console.log(res.data);
    setData(res.data)
    })
.catch((e) => {});
};
useEffect(() => initializeEvent(), []);

const deleteData = (id) => {
    axios.delete(`http://crmtesting.tdc3030.com/api/Chat/deleteViewEmp/` +id)
    .then((res) => {
    if (res.status === 200) {
      alert("Successfully deleted");
      window.location.reload();
    } else Promise.reject();
    })
      .catch((err) => alert("Something went wrong"));
    };

    const [view, setView] = useState([])
    const editTest=(id)=>{
        console.log('getdata',id)
        setViewEmp(id);
        setView(id.view)
}
      
//console.log('eeditsdata',viewEmp)
//const [item, setItem] = useState([])
const handleEdit = (item) => {
    // console.log(item)
      navigate("/updateAddEmp", {
        state: {item
        },
      });
   };;

const handleChangeInput = (e) => {
    let key = e.target.name;
    let value = e.target.value;
    const _currentData = { ...viewEmp, [key]: value };
    setViewEmp(_currentData);
    axios
    .post(`http://crmtesting.tdc3030.com/api/Chat/fetchByEmpName`, _currentData)
    .then((res) => {
       console.log('response',res.data)
       setData(res.data)
    })
    console.log(_currentData)
};

ReactHTMLTableToExcel.format = (s, c) => {
    if (c && c['table']) {
    const html = c.table;
    const parser = new DOMParser();
    const doc = parser.parseFromString(html, 'text/html');
    const rows = doc.querySelectorAll('tr');
    
    for (const row of rows) row.removeChild(row.firstChild);
    
    c.table = doc.querySelector('table').outerHTML;
    }
    
    return s.replace(/{(\w+)}/g, (m, p) => c[p]);
};

return(
<>
<EmpMngtDash/>
<div id="depVwEmp" className="scrollmenu">
<ReactHTMLTableToExcel id="font" className="btnVwEmp" table="viewEmpTbl" filename="EmployeesList" sheet="tablexls" buttonText="Excel"/>

{/* <div className="serchVwEmp">
<p>Search: <input type="search" name="firstName" value={viewEmp.firstName} onChange={handleChangeInput} id='linetextSerh' placeholder="Search"/></p>
</div> */}

<div>
  <table id="viewEmpTbl" className="tabdis">
  <ul>
  <thead>
      <tr>
          <th>Sl.No</th>
          <th>Full Name</th>
          <th>View</th>
          <th>Edit</th>
          <th>Delete</th>
       </tr>
  </thead>
  <tbody>
      {
          data.map((data, index)=> (
                  <tr key={index}>
                    <td>{index+1}</td>
                    <td>{data.firstName} {data.lastName}</td>
                    <td><button value="ViewEmp"  onClick={()=>editTest(data)}  data-toggle="modal" data-target="#editViewEmp" id="btnIcon"><i class='fa fa-eye' id="faIcon2"></i></button> </td>
                    <td><button value="EditViewEmp"  onClick={() => handleEdit(data)} id="btnIcon"><i class='fa fa-edit' id="faIcon1"></i></button> </td>
                    <td><button value="DeleteViewEmp" onClick={()=>deleteData(data.id)} id="btnIcon"><i class='fa fa-trash' id="faIcon"></i></button> </td>
                  </tr>))
      }
  </tbody>
  </ul>
  </table>
  </div>
  </div>

{/*-----------------------------------------Modal view employee---------------------------------------*/}
  <div class="modal fade" id="editViewEmp" role="dialog">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal">&times;</button>
          <h4 class="modal-title">View Employee Details</h4>
        </div>
        <div class="modal-body">
        <div>

        <div id='isAddPos'>
        <p id='pVwEmp'>First Name: <input id='linetext' value={viewEmp.firstName}/> </p> 
        <p id='pVwEmpLN'>Last Name: <input id='linetext' value={viewEmp.lastName}/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Official Email: <input value={viewEmp.officialEmail} id='linetext'/> </p> 
        <p id='pVwEmpPE'>Personal Email: <input value={viewEmp.personalEmail} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Phone Number: <input value={viewEmp.phoneNo} id='linetext'/> </p> 
        <p id='pVwEmpAPN'>Alt Phone Number: <input value={viewEmp.altPhoneNo} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Position: <input value={viewEmp.position} id='linetext'/> </p> 
        <p id='pVwEmpD'>Department: <input value={viewEmp.department} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        
        <div id='isAddPos'>
        <p id='pVwEmp'>Gender: <input value={viewEmp.gender} id='linetext'/> </p> 
        <p id='pVwEmpDOF'>Date of birth: <input value={viewEmp.dob} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Date of joining: <input value={viewEmp.doj} id='linetext'/> </p> 
        <p id='pVwEmpS'>Stipend: <input value={viewEmp.stipend} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Salary: <input value={viewEmp.salary} id='linetext'/> </p> 
        <p id='pVwEmpB'>Basic: <input value={viewEmp.basic} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>HRA: <input value={viewEmp.hra} id='linetext'/> </p> 
        <p id='pVwEmpG'>Gross: <input value={viewEmp.gross} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>PF: <input value={viewEmp.pf} id='linetext'/> </p> 
        <p id='pVwEmpIn'>Incentive: <input value={viewEmp.incentive} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        <div id='isAddPos'>
        <p id='pVwEmp'>Start time: <input value={viewEmp.start_time} id='linetext'/> </p> 
        <p id='pVwEmpET'>End time: <input value={viewEmp.end_time} id='linetext'/></p>  
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>
        
        <div id='isAddPos'>
        <p id='pVwEmp'>Address: <input value={viewEmp.address} id='linetext1'/> </p> 
        <p style={{ marginTop: "1px", color: "red" }}></p>
        </div>

        </div>
        </div>
        <div class="modal-footer" id='ft'>
        <button id='modcl' type="button" class="btn btn-info btn-sm" data-dismiss="modal" >Close</button>
        </div>
      </div>
    </div>
  </div>
</>
)
}
export default ViewEmp;