import React, {useState, useEffect} from 'react';
import axios from "axios";
import {useNavigate, useLocation} from 'react-router-dom';
import env from "react-dotenv";
import { Bar } from "react-chartjs-2";
import ReportsDash from "./ReportsDash";
import { Chart, registerables } from 'chart.js';
Chart.register(...registerables); 

function EmpTable(){
  const navigate = useNavigate();

  var config = {
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      'accept': '*/*',
    },
};

//let Base_URL = env.API_URL;

const [employeeLogin, setEmployeeLogin] = useState({
  department: "",
  firstName: "",
  hr_supr: "",
  emp_number: "",
  emp_post: "",
  date_hired: "",
  emp_status: "",
  attendance: "",
  job_knw_skill: "",
  qua_work: "",
  invt_movt: "",
  team_work: "",
  gen_condt: "",
  discipline: "",
  tot_score: "",
  emp_comm: "",
  comm_prf: "",	
  rev_date: "",
  rev_todate: "",
  });

const resetEmployeeDetails = () => {
  let _resetEmployee = {
  department: "",
  firstName: "",
  hr_supr: "",
  emp_number: "",
  emp_post: "",
  date_hired: "",
  emp_status: "",
  attendance: "",
  job_knw_skill: "",
  qua_work: "",
  invt_movt: "",
  team_work: "",
  gen_condt: "",
  discipline: "",
  tot_score: "",
  emp_comm: "",
  comm_prf: "",	
  rev_date: "",
  rev_todate: "",
  };
  setEmployeeLogin(_resetEmployee);
};

const [item,setItem] = useState([])
  const [data,setData] = useState([])
  const initializeEvent = () => {
  axios.get(`http://crmtesting.tdc3030.com/api/EmpReg/getEmpData1`,config)
  .then((res) => {
  console.log(res);
  setData(res.data)
  })
  };
  useEffect(() => initializeEvent(), []);

  
   const handleChangeInput = (e) => {
    let key = e.target.name;
    let value = e.target.value;
    const _currentData = { ...employeeLogin, [key]: value };
    setEmployeeLogin(_currentData);
    axios
            .post(`http://crmtesting.tdc3030.com/api/EmpReg/fetchEmp1`, _currentData,config)
            .then((res) => {
               console.log('response',res.data)
               setItem(res.data)
            })
   // console.log(_currentData);
  };
  const barChartData = {
    labels: item.map(x=>x.rev_date),
    datasets: [
      {
        axis: "y",
        data: item.map(x=>x.attendance) ,
        label: "Attendance",
        borderColor: "#3333ff",
        backgroundColor: "rgba(0, 0, 255, 0.5)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.job_knw_skill) ,
        label: "Job Knowledge and Skills",
        borderColor: "#7fffd4",
        backgroundColor: "rgba(127, 255, 212)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.qua_work) ,
        label: "Quality of work",
        borderColor: "#ff00ff",
        backgroundColor: "rgba(255, 0, 225)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.invt_movt) ,
        label: "Initiative and Motivation",
        borderColor: "#00bfff",
        backgroundColor: "rgba(0, 191,225)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.team_work) ,
        label: "Team work",
        borderColor: "#00ffff",
        backgroundColor: "rgba(0, 255, 255)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.gen_condt) ,
        label: "General Conduct",
        borderColor: "#ff7f50",
        backgroundColor: "rgba(225, 127, 80)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.discipline) ,
        label: "Discipline",
        borderColor: "#ffff00",
        backgroundColor: "rgba(225, 225, 0)",
        fill: true
      },
      {
        axis: "y",
        data: item.map(x=>x.tot_score) ,
        label: "Rating",
        borderColor: "#b22222",
        backgroundColor: "rgba(178, 34, 34)",
        fill: true
      },
    ]
  };


  const handleChangeInput1 = (e) => {
    let key = e.target.name;
    let value = e.target.value;
    const _currentData = { ...employeeLogin, [key]: value };
    setEmployeeLogin(_currentData);
    console.log(_currentData)
  };
  
  const fetchDateRng = () => {
   
    let _requestData = {
      ...employeeLogin,
    };

   console.log(_requestData)
    {
       axios
            .post(`http://crmtesting.tdc3030.com/api/EmpReg/fetchDateRng`, _requestData,config)
            .then((res) => {
               console.log('response',res.data)
               setItem(res.data)
               if (res.data != "Failed") {
                 localStorage.setItem(
                  "user_info",
                   JSON.stringify(res.data)
                 );
               }
               if (res.data == "Failed") {
                alert("Invalid Credantials")
               }
               resetEmployeeDetails();
            })
            .catch((e) => {
               if (e.res.data.message == "Incorrect Password") {
              {
                 alert("Account Not Found");
               }
              }
            })
    }
  };

  const [attendance, setAttendance] = useState(false);
  const [job_knw_skill, setJob_knw_skill] = useState(false);
  const [qua_work, setQua_work] = useState(false);
  const [invt_movt, setInvt_movt] = useState(false);
  const [team_work, setTeam_work] = useState(false);
  const [gen_condt, setGen_condt] = useState(false);
  const [discipline, setDiscipline] = useState(false);
  const [tot_score, setTot_score] = useState(false);


 
  const [style_att, setStyle_att] = useState();
  const [style_job, setStyle_job] = useState();
  const [style_qua, setStyle_qua] = useState();
  const [style_invt, setStyle_invt] = useState();
  const [style_team, setStyle_team] = useState();
  const [style_gen, setStyle_gen] = useState();
  const [style_disp, setStyle_disp] = useState();
  const [style_scr, setStyle_src] = useState();

  const changeStyle = (e) => {
    const selectedValue = e.target.value;
    console.log("you just clicked");
    if(selectedValue=="attendance"){
    setStyle_att("cont2");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
    }
   if (selectedValue=="job_knw_skill") {
    setStyle_job("cont2");
    setStyle_att("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="qua_work") {
    setStyle_qua("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="invt_movt") {
    setStyle_invt("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="team_work") {
    setStyle_team("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="gen_condt") {
    setStyle_gen("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_disp("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="discipline") {
    setStyle_disp("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_src("ref");
   } 
   if (selectedValue=="tot_score") {
    setStyle_src("cont2");
    setStyle_att("ref");
    setStyle_job("ref");
    setStyle_qua("ref");
    setStyle_invt("ref");
    setStyle_team("ref");
    setStyle_gen("ref");
    setStyle_disp("ref");
   } 
  };

return (
<>
<ReportsDash/>
<div id="depRep" className="scrollmenu">
<div>
<p className="col-10 phone-col-12">Name</p>
<select type="text" name='firstName' value={employeeLogin.firstName} onChange={handleChangeInput} id='titTable'>
<option  value="" selected disabled>Select</option><br />
{data.map(data => (
  <option value={data.firstName} key={data.id}>{data.firstName}</option>
))
}
</select>
</div>

<div id="dateDsg">
<p className="col-10 phone-col-12">From</p>
<input className="input textsingle item" type="date" name='rev_date' value={employeeLogin.rev_date} onChange={handleChangeInput1} id='tit3' />
</div>

<div id="dateDsg1">
<p className="col-10 phone-col-12">To</p>
<input className="input textsingle item" type="date" name='rev_todate' value={employeeLogin.rev_todate} onChange={handleChangeInput1} id='tit3' />
</div>

<div id="btnDsg">
<button type="submit" value="Submit" className="btnForm" data-input="datetime" id='font' onClick={fetchDateRng}>Submit</button>
</div>

<div className="perfDsg">
<p className="col-10 phone-col-12">Performance</p>
  <select type="text" id='titTable' onChange={changeStyle}>
  <option  value="" selected disabled size={10}>Select</option><br />
        <option value='attendance'>Attendance</option>
        <option value='job_knw_skill'>Job Knowledge and Skills</option>
        <option value='qua_work'>Quality of Work</option>
        <option value='invt_movt'>Initiative and Motivation</option>
        <option value='team_work'>Teamwork</option>
        <option value='gen_condt'>General Conduct</option>
        <option value='discipline'>Discipline</option>
        <option value='tot_score'>Rating</option>
  </select>
  </div>

<div >
  <table id="tabdis">
  <ul>
  <thead>
      <tr>
          <th>Id</th>
          <th>Department</th>
          <th>Name</th>
          <th>HR/Supervisor</th>
          <th>Emp_Num</th>
          <th>Post</th> 
          <th>Date_Hired</th>
          <th>Emp_status</th>
          <th className={style_att}>Attendance</th>
          <th className={style_job}>Job Knowledge and Skills</th>
          <th className={style_qua}>Quality of work</th>
          <th className={style_invt}>Initiative and Motivation</th>
          <th className={style_team}>Teamwork</th>
          <th className={style_gen}>General Conduct</th>
          <th className={style_disp}>Discipline</th>
          <th className={style_scr}>Rating</th>
          <th>Appraisal Period</th>
          <th>Comments</th>
          <th>Rev_date</th>
          <th>Rev_time</th>
          {/* <th><input type="submit" value="Fetch" onClick={initializeEvent} />fetch</th>  */}
       </tr>
  </thead>
  <tbody>
      {
          item.map(item => (
                  <tr key={item.id}>
                      <td>{item.id}</td>
                      <td>{item.department}</td>
                      <td>{item.firstName}</td>
                      <td>{item.hr_supr}</td>
                      <td>{item.emp_number}</td>
                      <td>{item.emp_post}</td>
                      <td>{item.date_hired}</td>
                      <td>{item.emp_status}</td>
                      <td className={style_att}>{item.attendance}</td>
                      <td className={style_job}>{item.job_knw_skill}</td>
                      <td className={style_qua}>{item.qua_work}</td>
                      <td className={style_invt}>{item.invt_movt}</td>
                      <td className={style_team}>{item.team_work}</td>
                      <td className={style_gen}>{item.gen_condt}</td>
                      <td className={style_disp}>{item.discipline}</td>
                      <td className={style_scr}>{item.tot_score}</td>
                      <td>{item.emp_comm}</td>
                      <td>{item.comm_prf}</td>
                      <td>{item.rev_date}</td>
                      <td>{item.rev_time}</td>
                  </tr>))
      }
  </tbody>
  </ul>
  </table>
  </div>

</div>


<div id='barchart'>
  <Bar
    id='bar'
    type="bar"
    options={{
      title: {
        display: true,
        text: "COVID-19 Cases of Last 6 Months",
        fontSize: 20
      },
      legend: {
        display: true, 
        position: "top" 
      }
    }}
    data={barChartData}
  />
  </div>
  </>
  
);
}
export default EmpTable;