import React, {useState, useEffect} from 'react';
import axios from "axios";
import {useNavigate, useLocation} from 'react-router-dom';
import env from "react-dotenv";
import EmpMngtDash from "./EmpMngtDash";

const UpdateEmpBankDtls = () => {
 // let Base_URL = env.API_URL;

 const { state } = useLocation();
 
 console.log('data status',state.item)
   const navigate = useNavigate();
 
   var config = {
     headers: {
       'Content-Type': 'application/x-www-form-urlencoded',
       'accept': '*/*',
     },
   };

   const [formErrors, setFormErrors] = useState({});

   const validate = () => {
   const errors = {};
   const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;
   const alpanumricalValue =  /^(?=.*[a-zA-Z])(?=.*[0-9])[A-Za-z0-9]+$/;
   const accountNoRegaex= /^\d{9,18}$/;
   
   if (!eventDetails.firstName) {
       errors.firstName = "Name is required";
   }
   if (eventDetails.firstName && !nameRegex.test(eventDetails.firstName)) {
   errors.firstName = "Give proper name";
   }
   if (!eventDetails.nameOfBank) {
       errors.nameOfBank = "Name is required";
   }
   if (eventDetails.nameOfBank && !nameRegex.test(eventDetails.nameOfBank)) {
   errors.nameOfBank = "Give proper name";
   }
   if (!eventDetails.accountNo) {
       errors.accountNo = "Name is required";
   }
   if (eventDetails.accountNo && !accountNoRegaex.test(eventDetails.accountNo)) {
   errors.accountNo = "Max it contain 9 numbers and less than 18";
   }
   if (!eventDetails.ifscCode) {
       errors.ifscCode = "Name is required";
   }
   if (eventDetails.ifscCode && !alpanumricalValue.test(eventDetails.ifscCode)) {
   errors.ifscCode = "Give proper value";
   }
   if (!eventDetails.bankAccHolderName) {
       errors.bankAccHolderName = "Name is required";
   }
   if (eventDetails.bankAccHolderName && !nameRegex.test(eventDetails.bankAccHolderName)) {
   errors.bankAccHolderName = "Give proper name";
   }
   if (!eventDetails.upiId) {
       errors.upiId = "Name is required";
   }
   if (eventDetails.upiId && !alpanumricalValue.test(eventDetails.upiId)) {
   errors.upiId = "Give proper value";
   }
   return errors;
   };
   
   const validateOnChange = (name, value) => {
     const errors = {};
     const nameRegex = /^[A-Za-z\s]{1,}[\.]{0,1}[A-Za-z\s]{0,}$/;
     const phoneNumberRegex = /^(\+91[-\s]?)?[0]?(91)?[6789]\d{9}$/;
     const alpanumricalValue = /^(?=.*[a-zA-Z])(?=.*[0-9])[A-Za-z0-9]+$/;
     const accountNoRegaex= /^\d{9,18}$/;
    
     if (name == "firstName" && !value) {
       errors.firstName = "Name is required";
     }
     if (name == "firstName" && value && !nameRegex.test(value)) {
       errors.firstName = "Give proper name";
     } 
     if (name == "nameOfBank" && !value) {
       errors.nameOfBank = "Name is required";
     }
     if (name == "nameOfBank" && value && !nameRegex.test(value)) {
       errors.nameOfBank = "Give proper name";
     } 
     if (name == "accountNo" && !value) {
       errors.accountNo = "Name is required";
     }
     if (name == "accountNo" && value && !accountNoRegaex.test(value)) {
       errors.accountNo = "Max it contain 9 numbers and less than 18";
     } 
     if (name == "ifscCode" && !value) {
       errors.ifscCode = "Name is required";
     }
     if (name == "ifscCode" && value && !alpanumricalValue.test(value)) {
       errors.ifscCode = "Give proper value";
     } 
     if (name == "bankAccHolderName" && !value) {
       errors.bankAccHolderName = "Name is required";
     }
     if (name == "bankAccHolderName" && value && !nameRegex.test(value)) {
       errors.bankAccHolderName = "Give proper name";
     } 
     if (name == "upiId" && !value) {
       errors.upiId = "Name is required";
     }
     if (name == "upiId" && value && !alpanumricalValue.test(value)) {
       errors.upiId = "Give proper value";
     } 
     
     return errors;
   };
   
     const [data, setData] = useState([])
     const handleChangeInput = (e) => {
     let key = e.target.name;
     let value = e.target.value;
     const _currentData = { ...eventDetails, [key]: value };
     setEventDetails(_currentData);
     console.log(_currentData)
     const _errors = validateOnChange(e.target.name, e.target.value);
     if (Object.keys(_errors).length != 0) {
       let finalErrors = {
         ...formErrors,
         [key]: _errors[key],
       };
       setFormErrors(finalErrors);
     } else {
       let finalErrors = {
         ...formErrors,
         [key]: _errors[key],
       };
       setFormErrors(finalErrors);
     }
     
   };
 
    const [eventDetails, setEventDetails] = useState({
    id: "",
    firstName: "",
    nameOfBank: "",
    accountNo: "",
    ifscCode: "",
    bankAccHolderName: "",
    upiId: "",	
   });
 
   console.log('events',eventDetails)
    const [eventTableDetails, setEventTableDetails] = useState(null);
    const initializeEvent = () => {
     axios
       .get(`http://crmtesting.tdc3030.com/api/Chat/getEmpBankDtls/${state.item.id}`)
       .then((response) => {
         setEventTableDetails(response.data);
         //console.log(response)
          if (response.data != null) {
            let requestForSet = {
              id: response.data.id,
              firstName: response.data.firstName,
              nameOfBank: response.data.nameOfBank,
              accountNo: response.data.accountNo,
              ifscCode: response.data.ifscCode,
              bankAccHolderName: response.data.bankAccHolderName,
              upiId: response.data.upiId,
            };
            setEventDetails(requestForSet);
          }
       })
       .catch((e) => {});
   };
   
   useEffect(() => initializeEvent(), []);
   console.log('response',eventTableDetails)
 
   const updateData =()=>{
         let formdata = {
            'id':eventDetails.id,
            'firstName':eventDetails.firstName,
            'nameOfBank':eventDetails.nameOfBank,
            'accountNo':eventDetails.accountNo,
            'ifscCode':eventDetails.ifscCode,
            'bankAccHolderName':eventDetails.bankAccHolderName,
            'upiId':eventDetails.upiId
         }
   console.log('submitdata',formdata);
   axios.post("http://crmtesting.tdc3030.com/api/Chat/updateEmpBankDtls/", formdata, config)
   .then((res) => {
   console.log(res)
   if (res.status === 200) {
     alert("Successfully updated");
     } else Promise.reject();
      })
   .catch((err) => alert("Something went wrong"));
     console.log(formdata)
     }
 
return(
    <>
    <EmpMngtDash/>
<section>  
<ul>    

<div id='depbank'>
<p>Select Employee</p>
<input type="text" id='tit2' name="firstName" value={eventDetails.firstName} onChange={handleChangeInput} error="!eventDetails.firstName"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.firstName}</p>

<div id="nob">
<p>Name Of Bank</p>
<input type="text" id='tit2' name="nameOfBank" value={eventDetails.nameOfBank} onChange={handleChangeInput} error="!eventDetails.nameOfBank"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.nameOfBank}</p>
</div>

<p>Account Number</p>
<input type="text" id='tit2' name="accountNo" value={eventDetails.accountNo} onChange={handleChangeInput} error="!eventDetails.accountNo"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.accountNo}</p>

<div id="nob">
<p>IFSC Number</p>
<input type="text" id='tit2' name="ifscCode" value={eventDetails.ifscCode} onChange={handleChangeInput} error="!eventDetails.ifscCode"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.ifscCode}</p>
</div>

<p>Bank Account Holder Name</p>
<input type="text" id='tit2' name="bankAccHolderName" value={eventDetails.bankAccHolderName} onChange={handleChangeInput} error="!eventDetails.bankAccHolderName"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.bankAccHolderName}</p>

<div id="nob">
<p>UPI Id</p>
<input type="text" id='tit2' name="upiId" value={eventDetails.upiId} onChange={handleChangeInput} error="!eventDetails.upiId"/>
<p style={{ marginTop: "1px", color: "red" }}>{formErrors.upiId}</p>
</div>

<div onClick={()=>navigate("/viewEmpBankDtls")}>
<button className="template-preview-input add-button rhs-select col-2 phone-col-3, date, box" onClick={()=>updateData()} data-input="datetime" id='font'>
<span className="phone-hide, date">Submit</span>
</button> 
</div>

</div>

</ul>
</section>
</>
)
}
export default UpdateEmpBankDtls;